(function ()
{
    'use strict';

    angular
        .module('app.consumer',
            [
                'app.consumer.account',
                'app.consumer.alerts',
                'app.consumer.reports',
                'app.consumer.sample'
            ]
        )
        .config(config)
        .run( runBlock );

    /** @ngInject */
    function runBlock( msNavigationService, $firebaseAuth, jwtHelper ) {

        $firebaseAuth().$requireSignIn()
                    .then(function( user ) {
                        user.getToken().then(function( token ) {

                            var currentUser = jwtHelper.decodeToken( token );
                            
                            var shouldHide  = true;
                            
                            if ( currentUser.roles.customerAccount ) {
                                shouldHide  = false;
                            }

                            function isHidden() {
                                return shouldHide;
                            }

                            // Navigation
                            msNavigationService.saveItem('account', {
                                title    : 'Account',
                                icon     : 'icon-account',
                                state    : 'app.consumer_account',
                                hidden   : isHidden
                            });
                    
                            msNavigationService.saveItem('alerts', {
                                title    : 'Alerts',
                                icon     : 'icon-alert',
                                state    : 'app.consumer_alerts',
                                hidden   : isHidden
                            });
                    
                            msNavigationService.saveItem('reports', {
                                title    : 'Reports',
                                icon     : 'icon-chart-areaspline',
                                state    : 'app.consumer_reports',
                                hidden   : isHidden
                            });
                            
                            msNavigationService.saveItem('dashboard', {
                                title    : 'Dashboard',
                                icon     : 'icon-tile-four',
                                state    : 'app.consumer_sample',
                                weight   : 0,
                                hidden   : isHidden
                            });
                        })
                    });
                    
    };


    /** @ngInject */
    function config( API_URL, msApiProvider )
    {
        // Api
        var methodOverrides = {
            get     : { isArray : true },
            update  : { method : 'PUT' }
        };
        
        var paramDefaults   = {
            id      : '@_id'
        };

        msApiProvider.setBaseUrl( '/' );

        msApiProvider.register( 'meta', [ 'app/data/shared/meta.json' ] );
        
        msApiProvider.register( 'provinces', [ 'app/data/shared/provinces.json', {}, { get : methodOverrides.get } ] );

        msApiProvider.setBaseUrl( API_URL );

        msApiProvider.register( 'customerAccount', [ '/me/customer-account' ]);

        var chartDataParamDefaults   = {
            interval    : 'hour',
            start       : '1 day ago'
        }
        
        msApiProvider.register( 'chartData', [ '/chartdata', chartDataParamDefaults ] );
        
        msApiProvider.register( 'networkHubEvents', [ '/search', { limit: 10000, msgType: 2, start: "'30 days ago'" }, { get : methodOverrides.get } ]);
        
        msApiProvider.register( 'sensorHubEvents', [ '/search', { limit: 10000, msgType: 4, start: "'30 days ago'" }, { get : methodOverrides.get } ]);

        msApiProvider.register( 'consumerSensorHubs', [ '/sensor-hubs/:id', paramDefaults, methodOverrides ]);
        
        msApiProvider.register( 'user', [ '/users/:id', paramDefaults, { update : methodOverrides.update }]);
    }

})();

angular
    .module( 'app.consumer' )
    .factory( 'AlertFormatter', AlertFormatter );


/** @ngInject */
function AlertFormatter() {
  return {
    text: function( event ) {
        var alertText, eventDate, eventResolved, eventType;
        eventResolved = event.sensorEventEnd !== 0;
        if (eventResolved) {
            eventType = event.sensorEventEnd;
        } else {
            eventType = event.sensorEventStart;
        }
        alertText = (function() {
            switch (eventType) {
            case 1:
                return 'Water detect';
            case 2:
                return 'Motion detect';
            case 3:
                return 'Low temperature';
            case 4:
                return 'High temperature';
            case 5:
                return 'Low humidity';
            case 6:
                return 'High humidity';
            case 7:
                return 'Low light';
            case 8:
                return 'High light';
            case 9:
                return 'Movement';
            }
        })();
        if (eventResolved) {
            alertText += ' resolved';
        }
        return alertText;
    },

    iconAndColor: function( event ) {
        var icon, eventDate, eventResolved, eventType;
        eventResolved = event.sensorEventEnd !== 0;
        if (eventResolved) {
            eventType = event.sensorEventEnd;
        } else {
            eventType = event.sensorEventStart;
        }
        icon = (function() {
            switch (eventType) {
            case 1:
                return ['icon-water', '#0091EA'];
            case 2:
                return ['icon-eye', '#9C27B0'];
            case 3:
                return ['icon-thermometer', '#F44336'];
            case 4:
                return ['icon-thermometer', '#F44336'];
            case 5:
                return ['icon-weather-hail','inherit'];
            case 6:
                return ['icon-weather-hail','inherit'];
            case 7:
                return ['icon-white-balance-sunny', '#FFB300'];
            case 8:
                return ['icon-white-balance-sunny', '#FFB300'];
            case 9:
                return ['icon-reply-all', '#388E3C'];
            }
        })();
        return icon;
    }
  }
}
(function ()
{
    'use strict';

    angular
        .module('app.consumer.sample')
        .controller('SampleController', SampleController);

    /** @ngInject */
    function SampleController( $document, $firebaseObject, $http, $mdDialog, $mdToast, $state, AlertFormatter, currentUser, meta, msApi, sensorHubs )
    {   
        var macAddress  = currentUser.roles.customerAccount.gateways[ 0 ]._id;
        var latestRef   = firebase.database().ref().child( macAddress );
        var vm          = this;
        
        if ( currentUser.roles.customerAccount.country === 'CA' ) {
            var weatherURL  = 'https://api.wunderground.com/api/ac02af3b799f05ef/conditions/q/Canada/' + encodeURIComponent( currentUser.roles.customerAccount.city ) + '.json' + '?callback=JSON_CALLBACK';

        } else {
            var weatherURL  = 'https://api.wunderground.com/api/ac02af3b799f05ef/conditions/q/' + currentUser.roles.customerAccount.state + '/' + encodeURIComponent( currentUser.roles.customerAccount.city ) + '.json' + '?callback=JSON_CALLBACK';
        }


        // Data
        vm.alertFormatter   = AlertFormatter;
        vm.currentUser      = currentUser;
        vm.firstName        = currentUser.roles.customerAccount.name.first;
        vm.forms            = {};
        vm.latest           = $firebaseObject( latestRef );
        vm.macAddress       = macAddress;
        vm.meta             = meta;
        vm.sensorHubs       = sensorHubs;


        // Methods
        // TODO: make this 24 hours
        vm.alertInLast12Hrs = function( sensorHub ) {
            var msIn12Hrs, timeSince;
            if (!(sensorHub && sensorHub.latestAlert)) {
                return;
            }
            msIn12Hrs = 43200 * 1000;
            // TODO: hack
            msIn12Hrs = msIn12Hrs * 2;
            timeSince = (new Date().getTime()) - sensorHub.latestAlert.updateTime;
            return timeSince <= msIn12Hrs;
        };

        vm.cssClassByRssiThreshold = function( rssi ) {
            var rssiNum;
            if ( rssi === void 0 ) {
                return '';
            }
            rssiNum = Number( rssi );
            switch ( false ) {
                case !( rssiNum < -95 ):
                return 'error';
                case !( rssiNum < -80 ):
                return 'warn';
                default:
                return 'normal';
            }
        };
        
        vm.getSensorHubById = function( id ) {
            return vm.sensorHubs.find(function( sh ) {
                return sh._id === id;
            })
        }

        vm.openAlertSetupDialog = function( ev, sensorHub ) {
            $mdDialog.show({
                controller         : 'AlertSetupDialogController',
                controllerAs       : 'vm',
                templateUrl        : 'app/consumer/sample/dialogs/alert-setup/alert-setup-dialog.html',
                parent             : angular.element( $document.find( '#content-container' ) ),
                targetEvent        : ev,
                clickOutsideToClose: true,
                locals             : {
                    meta        : meta,
                    sensorHub   : sensorHub
                }
            });
        };

        // console.log( Auth );
        
        vm.save                 = function ( sensorHub ) {
            msApi.request( 'consumerSensorHubs@update', sensorHub ).then(function( resp ) {
                
                var message = 'Room names will be updated next time you log in.';

                $state.reload().then(function() {
                
                    $mdToast.show({
                        template : '<md-toast id="language-message" layout="column" layout-align="center start"><div class="md-toast-content">' + message + '</div></md-toast>',
                        hideDelay: 7000,
                        position : 'top right',
                        parent   : '#content'
                    });  

                })

            });
        }

        //////////
        $http.jsonp( weatherURL, {} ).then(function( resp ) {
            vm.weather  = resp.data.current_observation;
        });
    }
})();

